const gameScene = new Phaser.Class({
  Extends: Phaser.Scene,

  initialize: function GameScene() {
    Phaser.Scene.call(this, { key: 'gameScene' });
  },

  preload() {
    this.load.spritesheet('tHex', 'https://upload.wikimedia.org/wikipedia/commons/2/22/Chromium_T-Rex-offline-trex.png', {
      frameWidth: 44,
      frameHeight: 47,
    });
  },

  create() {
    const tHex = this.physics.add.sprite(100, 300, 'tHex');
    tHex.setGravityY(800);

    const ground = this.physics.add.staticImage(400, 400, 'https://i.stack.imgur.com/noZPq.png');
    ground.setScale(2).refreshBody();
    // Collide the T-Rex sprite with the ground
    this.physics.add.collider(tHex, ground);
  },

  update() {
    // Обновление игровой сцены
  },
});

export { gameScene };
